import Link from "next/link"

function PageSelection() {
      const pathname = usePathname(); // Rota atual
  const contentLocal = content.header.links;

    return (
        <ul className="flex gap-4">
        {contentLocal.map((item, index) => (
          <li key={index}>
            <Link
              href={item.url}
              className={`px-2 py-1 inline-flex items-center justify-center rounded-full text-center ${
                pathname === item.url ? "bg-white shadow-md" : ""
              }`}
            >
              {item.label}
            </Link>
          </li>
        ))}
      </ul>
    )
}

export default PageSelection
